import React, { useState } from 'react';
import Checkbox from '../../checkbox';
import Col from '../../col';
import Grid from '../../grid';
import Listbox from '../../listbox';
import ListboxContainer from '../../listbox-container';
import ListboxItem from '../../listbox-item';
import Row from '../../row';
import Typography from '../../typography';

const ListboxExample = () => {
  const [stateOne, setStateOne] = useState({
    items: [
      {
        label: 'Silver Signature Card',
        value: 'silver-signature-card'
      }, {
        label: 'Visa Signature Card',
        value: 'visa-signature-card'
      }, {
        label: 'Platinum Visa Card',
        value: 'platinum-visa-card'
      }, {
        label: 'Premium Card',
        value: 'premium-card'
      }, {
        label: 'Another Card',
        value: 'another-card'
      }, {
        label: 'Other Card',
        value: 'other-card'
      }, {
        label: 'Secondary Card',
        value: 'secondary-card'
      }, {
        label: 'Tertiary Card',
        value: 'tertiary-card'
      }, {
        label: 'Gold Signature Card',
        value: 'gold-signature-card'
      }, {
        label: 'Yet Another Card',
        value: 'yet-another-card'
      }, {
        label: 'Last Card',
        value: 'last-card'
      }
    ],
    selected: ['platinum-visa-card']
  });

  const [stateTwo, setStateTwo] = useState({
    items: [
      {
        label: 'Silver Signature Card',
        value: 'silver-signature-card'
      }, {
        label: 'Visa Signature Card',
        value: 'visa-signature-card'
      }, {
        label: 'Platinum Visa Card',
        value: 'platinum-visa-card'
      }, {
        label: 'Premium Card',
        value: 'premium-card'
      }, {
        label: 'Another Card',
        value: 'another-card'
      }, {
        label: 'Other Card',
        value: 'other-card'
      }, {
        label: 'Secondary Card',
        value: 'secondary-card'
      }, {
        label: 'Tertiary Card',
        value: 'tertiary-card'
      }, {
        label: 'Gold Signature Card',
        value: 'gold-signature-card'
      }, {
        label: 'Yet Another Card',
        value: 'yet-another-card'
      }, {
        label: 'Last Card',
        value: 'last-card'
      }
    ],
    selected: ['silver-signature-card']
  });

  const handleOnSelectOne = (e, { selected }) => setStateOne({
    ...stateOne,
    selected
  });
  const handleOnSelectTwo = (e, { selected }) => setStateTwo({
    ...stateTwo,
    selected
  });

  return (
    <Grid>
      <Row>
        <Col>
          <Typography tag="h2">Listbox with Listbox Container Wrapper for functionality</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <ListboxContainer label="Select your card type" name="listbox-container-demo-1">
            <Listbox>
              <ListboxItem listItemValue="visa-signature-card">Visa Signature Card</ListboxItem>
              <ListboxItem listItemValue="platinum-visa-card">Platinum Visa Card</ListboxItem>
              <ListboxItem listItemValue="premium-card">Premium Card</ListboxItem>
              <ListboxItem listItemValue="another-card">Another Card</ListboxItem>
              <ListboxItem listItemValue="other-card">Other Card</ListboxItem>
              <ListboxItem listItemValue="secondary-card">Secondary Card</ListboxItem>
              <ListboxItem listItemValue="tertiary-card">Tertiary Card</ListboxItem>
              <ListboxItem listItemValue="gold-signature-card">Gold Signature Card</ListboxItem>
              <ListboxItem listItemValue="yet-another-card">Yet Another Card</ListboxItem>
              <ListboxItem listItemValue="last-card">Last Card</ListboxItem>
            </Listbox>
          </ListboxContainer>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Listbox with Listbox Container Wrapper for functionality (Multiselect)</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <ListboxContainer label="Select your card type" name="listbox-container-demo-2">
            <Listbox multiselect>
              <ListboxItem listItemValue="visa-signature-card">
                <Checkbox label="Visa Signature Card" name="listbox-checkbox" tabIndex="-1" />
              </ListboxItem>
              <ListboxItem listItemValue="platinum-visa-card">
                <Checkbox label="Platinum Visa Card" name="listbox-checkbox" tabIndex="-1" />
              </ListboxItem>
              <ListboxItem disabled listItemValue="premium-card">
                <Checkbox disabled label="Premium Card" name="listbox-checkbox" tabIndex="-1" />
              </ListboxItem>
              <ListboxItem listItemValue="another-card">
                <Checkbox label="Another Card" name="listbox-checkbox" tabIndex="-1" />
              </ListboxItem>
              <ListboxItem listItemValue="other-card">
                <Checkbox label="Other Card" name="listbox-checkbox" tabIndex="-1" />
              </ListboxItem>
              <ListboxItem listItemValue="secondary-card">
                <Checkbox label="Secondary Card" name="listbox-checkbox" tabIndex="-1" />
              </ListboxItem>
              <ListboxItem disabled listItemValue="tertiary-card">
                <Checkbox disabled label="Tertiary Card" name="listbox-checkbox" tabIndex="-1" />
              </ListboxItem>
              <ListboxItem listItemValue="gold-signature-card">
                <Checkbox label="Gold Signature Card" name="listbox-checkbox" tabIndex="-1" />
              </ListboxItem>
              <ListboxItem listItemValue="yet-another-card">
                <Checkbox label="Yet Another Card" name="listbox-checkbox" tabIndex="-1" />
              </ListboxItem>
              <ListboxItem listItemValue="last-card">
                <Checkbox label="Last Card" name="listbox-checkbox" tabIndex="-1" />
              </ListboxItem>
            </Listbox>
          </ListboxContainer>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Listbox with Data and onSelect event</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <span id="listbox-onselect-example-values">
            {stateOne.selected ? `Value selected: ${stateOne.selected}` : 'Selected values will be shown here.'}
          </span>
          <ListboxContainer
            aria-describedby="listbox-onselect-example-values"
            label={'Select your card type'}
            name="listbox-container-demo-3"
            onSelect={handleOnSelectOne}
            selectedListItems={stateOne.selected}
          >
            <Listbox>
              {
                stateOne.items.map((item, k) => (
                  <ListboxItem key={k} listItemValue={item.value}>{item.label}</ListboxItem>
                ))
              }
            </Listbox>
          </ListboxContainer>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Listbox Multiselect with Data and onSelect event</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <span id="listbox-onselect-example-values--multiselect">
            {stateTwo.selected ? `Value selected: ${stateTwo.selected.join(', ')}` : 'Selected values will be shown here.'}
          </span>
          <ListboxContainer
            aria-describedby="listbox-onselect-example-values--multiselect"
            label={'Select your card type'}
            name="listbox-container-demo-5"
            onSelect={handleOnSelectTwo}
            selectedListItems={stateTwo.selected}
          >
            <Listbox multiselect>
              {
                stateTwo.items.map((item, k) => (
                  <ListboxItem key={k} listItemValue={item.value}>
                    <Checkbox label={item.label} name="listbox-checkbox-multi-2" tabIndex="-1" />
                  </ListboxItem>
                ))
              }
            </Listbox>
          </ListboxContainer>
        </Col>
      </Row>
    </Grid>
  );
};

export default ListboxExample;
