import React, { Children, cloneElement, forwardRef, useEffect, useRef, useState } from 'react';
import { arrayOf, element, func, node, number, oneOfType, string } from 'prop-types';
import classnames from 'classnames';
import shortid from 'shortid';
import ListboxLabel from '../listbox-label';
import ListboxInput from '../listbox-input';

const CSS_PREFIX = 'vds-listbox-container';

const keyCode = {
  DOWN: 40,
  END: 35,
  ESC: 27,
  HOME: 36,
  PAGEDOWN: 34,
  PAGEUP: 33,
  RETURN: 13,
  SPACE: 32,
  TAB: 9,
  UP: 38
};

const isPrintableCharacter = str => str.length === 1 && str.match(/\S/);
const getCharFilterIndex = ({
  char,
  count,
  focusableItems,
  start
}) => {
  for (let i = start; i < count; i++) {
    if (char === focusableItems[i].element.textContent.trim().toLowerCase().substring(0, 1)) {
      return i;
    }
  }
  return -1;
};

const scrollToItem = ({ element, listboxNode }) => {
  if (element && listboxNode && listboxNode.scrollHeight > listboxNode.clientHeight) {
    const scrollBottom = listboxNode.clientHeight + listboxNode.scrollTop;
    const elementBottom = element.offsetTop + element.offsetHeight;

    if (elementBottom > scrollBottom) {
      listboxNode.scrollTop = elementBottom - listboxNode.clientHeight;
    }

    if (element.offsetTop < listboxNode.scrollTop) {
      listboxNode.scrollTop = element.offsetTop;
    }
  }
};

const ListboxContainer = forwardRef(({
  children,
  className,
  id = shortid.generate(),
  label,
  name,
  onSelect,
  selectedListItems,
  tabIndex,
  ...remainingProps
}, ref) => {
  const [selected, setSelected] = useState(selectedListItems);
  const [focused, setFocused] = useState({});
  const [focusableItems, setFocusableItems] = useState([]);
  const [focusShow, setFocusShow] = useState(false);
  const uniqueId = useRef(id);
  const uid = uniqueId.current;
  const listboxRef = useRef();

  useEffect(() => {
    const arr = [];
    const pool = listboxRef.current.querySelectorAll(
      'ul > li'
    );

    if (listboxRef.current) {
      pool.forEach((e, i) => {
        if (e.className.indexOf('vds-state--disabled') < 0) {
          const obj = {
            element: e,
            value: e.dataset.listItemValue,
            index: i,
            arrayIndex: arr.length
          };

          arr.push(obj);

          if (Object.keys(focused).length === 0 && selectedListItems.indexOf(obj.value) >= 0) {
            setFocused(obj);
          }
        }
      });

      setFocusableItems(arr);
    }
  }, [focused, selectedListItems]);

  const getFocusableByValue = (value) => {
    return focusableItems.find(e => e.value === value);
  };

  const getFocusableByIndex = (index) => {
    return focusableItems.find(e => e.index === index);
  };

  const getFocusableByArrayIndex = (i) => {
    return focusableItems.find(e => e.arrayIndex === i);
  };

  const getFocusedPrevious = () => {
    const found = getFocusableByIndex(focused.index);
    const foundIndex = focusableItems.indexOf(found);
    const inbounds = found !== undefined && foundIndex - 1 >= 0;
    const previousIndex = inbounds ? foundIndex - 1 : 0;

    return focusableItems[previousIndex];
  };

  const getFocusedNext = (loop) => {
    const found = getFocusableByIndex(focused.index);
    const foundIndex = focusableItems.indexOf(found);
    const inbounds = found !== undefined && foundIndex + 1 <= focusableItems.length - 1;
    const nextIndex = inbounds ? foundIndex + 1 : loop ? 0 : foundIndex;

    return focusableItems[nextIndex];
  };

  const handleClick = e => {
    const value = e.currentTarget.dataset.listItemValue;
    const multiselect = listboxRef.current ? listboxRef.current.classList.contains('vds-select--multi') : false;
    let values = [];

    setFocused(getFocusableByValue(value));

    if (multiselect) {
      const includes = selected.includes(value);

      if (multiselect && includes) {
        values = [...selected.filter(item => item !== value)];
      }

      if (multiselect && !includes) {
        values = [...selected, value];
      }
    }

    if (!multiselect) {
      values = [value];
    }

    handleSelect(e, values);
  };

  const handleFocus = e => {
    setFocusShow(true);
  };

  const handleBlur = e => {
    if (e.currentTarget.classList.contains('vds-select--multi')) {
      setFocusShow(false);
    }
  };

  const handleKeyUp = e => {
    const code = e.charCode || e.keyCode;
    const value = Object.keys(focused).length > 0 ? focused.value : focusableItems[0].value;
    let multiselect = e.currentTarget.classList.contains('vds-select--multi');

    if (typeof focusedIndex === 'undefined' && code === keyCode.TAB) {
      setFocused(getFocusableByValue(value));

      if (!multiselect) {
        handleSelect(e, [value]);
      }
    }

    if (multiselect && ((code === keyCode.SPACE) || (code === keyCode.RETURN))) {
      if (typeof onSelect === 'function') {
        onSelect(e, { selected });
      }

      return;
    }

    if (code === keyCode.PAGEUP || code === keyCode.PAGEDOWN) {
      return;
    }

    scrollToItem({
      element: focused.element,
      listboxNode: listboxRef.current
    });
  };

  const handleKeyDown = e => {
    const code = e.charCode || e.keyCode;
    const count = focusableItems.length;
    const multiselect = e.currentTarget.classList.contains('vds-select--multi');
    let focusId = Boolean(focused) ? focused.index : 0;

    if (code === keyCode.END && count) {
      e.preventDefault();
      focusId = getFocusableByIndex(count - 1);

      setFocused(focusId);

      if (!multiselect) {
        handleSelect(e, [focusId.value]);
      }
    }

    if (code === keyCode.HOME && count) {
      e.preventDefault();
      focusId = getFocusableByIndex(0);

      setFocused(focusId);

      if (!multiselect) {
        handleSelect(e, [focusId.value]);
      }
    }

    if (code === keyCode.UP && count) {
      e.preventDefault();
      focusId = getFocusedPrevious();

      setFocused(focusId);

      if (!multiselect) {
        handleSelect(e, [focusId.value]);
      }
    }

    if (code === keyCode.DOWN && count) {
      e.preventDefault();
      focusId = getFocusedNext();

      setFocused(focusId);

      if (!multiselect) {
        handleSelect(e, [focusId.value]);
      }
    }

    if ((code === keyCode.RETURN || code === keyCode.SPACE) && count) {
      e.preventDefault();
      const includes = selected.includes(focused.value);

      if (multiselect) {
        if (includes) {
          handleSelect(e, [...selected.filter(item => item !== focused.value)]);
        } else {
          handleSelect(e, [...selected, focused.value]);
        }
      }
    }

    if ( e.ctrlKey || e.altKey || e.metaKey || code === keyCode.PAGEUP || code === keyCode.PAGEDOWN) {
      return;
    }

    if (isPrintableCharacter(e.key)) {
      const char = e.key.toLowerCase();
      const start = focused.arrayIndex + 1 === count ? 0 : focused.arrayIndex + 1;

      focusId = getCharFilterIndex({
        char,
        count,
        focusableItems,
        start
      });

      if (focusId === -1) {
        focusId = getCharFilterIndex({
          char,
          count,
          focusableItems,
          start: 0
        });
      }

      if (focusId > -1) {
        setFocused(getFocusableByArrayIndex(focusId));

        if (!multiselect) {
          handleSelect(e, [getFocusableByArrayIndex(focusId).value]);
        }
      }
    }

    if (Boolean(focusId)) {
      scrollToItem({
        element: typeof focusId === "number" ? focused.element : focusId.element,
        listboxNode: listboxRef.current
      });
    }
  };

  const handleSelect = (element, value) => {
    setSelected(value);

    if (typeof onSelect === 'function') {
      onSelect(element, { selected: value });
    }
  };

  return (
    <div
      className={classnames(CSS_PREFIX, className)}
      id={uid}
      ref={ref}
      {...remainingProps}
    >
      {label
        ? <ListboxLabel
          id={`${CSS_PREFIX}-listbox--label-${uid}`}
          htmlFor={`${CSS_PREFIX}-listbox-${uid}`}
        >
          {label}
        </ListboxLabel>
        : undefined}
      <ListboxInput name={name} value={selected.join(',')} />
      {
        cloneElement(children, {
          'aria-activedescendant': typeof focused.index === 'number' ? `${CSS_PREFIX}-listbox-item-${uid}-${focused.index}` : undefined,
          'aria-labelledby': children.props['aria-labelledby'] ? children.props['aria-labelledby'] : label ? `${CSS_PREFIX}-listbox--label-${uid}` : undefined,
          'aria-multiselectable': children.props.multiselect ? 'true' : undefined,
          children: Children.map(children.props.children, (child, k) => cloneElement(child, {
            'aria-selected': child.props.listItemValue
              ? selected.includes(child.props.listItemValue)
                ? 'true' : 'false'
              : 'false',
            children: children.props.multiselect
              ? Children.map(child.props.children, (checkbox, i) => cloneElement(checkbox, {
                checked: selected.includes(child.props.listItemValue),
                key: `${uid}-${k}-${i}`,
                value: child.props.listItemValue
              }))
              : child.props.children,
            focus: children.props.multiselect
              ? focusShow && child.props.listItemValue === focused.value
              : undefined,
            id: `${CSS_PREFIX}-listbox-item-${uid}-${k}`,
            key: `${uid}-${k}`,
            listItemValue: child.props.listItemValue ? child.props.listItemValue : `${uid}-${k}`,
            onClick: handleClick,
            role: 'option',
            selected: child.props.listItemValue && !children.props.multiselect
              ? selected.includes(child.props.listItemValue)
              : undefined,
            style: { position: 'relative' }
          })),
          id: `${CSS_PREFIX}-listbox-${uid}`,
          onBlur: handleBlur,
          onFocus: handleFocus,
          onKeyDown: handleKeyDown,
          onKeyUp: handleKeyUp,
          ref: listboxRef,
          role: 'listbox',
          style: { position: 'relative' },
          tabIndex
        })
      }
    </div>
  );
});

ListboxContainer.propTypes = {
  /**
  * @ignore
  */
  children: element,

  /**
   * @ignore
   */
  className: string,

  /**
   * ID
   */
  id: oneOfType([number, string]),

  /**
   * Label
   */
  label: node,

  /**
   * Name
   */
  name: string,

  /**
   * On Select
   */
  onSelect: func,

  /**
   * Selected List items
   */
  selectedListItems: arrayOf(string),

  /**
   * Tab Index
   */
  tabIndex: oneOfType([number, string])
};

ListboxContainer.defaultProps = {
  selectedListItems: [''],
  tabIndex: '0'
};

ListboxContainer.displayName = 'ListboxContainer';

export default ListboxContainer;
